html{
  background-color: rgb(239, 242, 250);
}
#name{
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
  font-size: 25px;
  color: rgb(36, 38, 53);
}
#nav-container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: rgb(173, 197, 241);
  height: 55px;
}

#nav-bar{
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  color: rgb(36, 38, 53);
  font-size: 20px;

}

.nav-tab{
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
  text-align: center;
  cursor: pointer;

}

.nav-tab:hover{
  background-color:rgba(110, 125, 153, 0.37);
  color: rgb(241, 241, 255);
}

.section-header{
  text-align: center;
  padding-top: 20px;
  font-size: 30px;
}

.section-container{
  padding: 20px;
}

#img-home{
  width: 250px;
  height: 350px;
  border: 8px solid white;
}

#home-container{
  display: flex;
  flex-direction: row;
  justify-content: center;
}

#home-blurb{
  padding: 15px;
  font-size: 20px;
  line-height: 1.5;

}

#about-container{
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: nowrap;
}

.about-blurb{
  display: flex;
  flex-direction: column;
  border-style: solid;
  border-width: 3px;
  border-radius: 10px;
  border-color: white;
  height: 550px;
  width: 25%;
  margin: 15px;
  padding: 10px;
  font-size: 20px;
  font-weight: bold;
  background-color: rgba(210, 210, 219, 0.425);
  color: rgb(32, 34, 51);
}

.about-img-container{
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.img-about{
  height: 200px;
  width: 200px;
  border-style: solid;
  border-width: 3px;
  border-radius: 10px;
  border-color: white;
  margin: 10px;
}

.about-bio{
  font-size: 16px;
  font-weight: normal;
  line-height: 1.5;
  text-align: center;
}

@media (max-width: 1300px){
  #name{
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 20px;
  }

  #nav-container{
    height: 45px;
  }
  
  #nav-bar{
    justify-content: flex-start;
    font-size: 15px;
  }
  
  .nav-tab{
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  
  .section-header{
    padding-top: 15px;
    font-size: 25px;
  }
  
  .section-container{
    padding: 20px;
  }
  
  #img-home{
    width: 175px;
    height: 245px;
    border: 6px solid white;
  }
  
  #home-blurb{
    padding: 10px;
    font-size: 12px;
    line-height: 1.5;
  }
  
  
  .about-blurb{
    width: 50%;
    height: 500px;
  }
  
  .about-img-container{
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  .img-about{
    height: 150px;
    width: 150px;
  }
  
  .about-bio{
    font-size: 13px;
  }

}
